@import "../../../bower_components/normalize.css/normalize.css";
@import "../../../bower_components/bootstrap-grid-css/grid.css";
@font-face {
  font-family: Effra;
  src: url(/assets/fonts/Effra_Lt.ttf);
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: Effra;
  src: url(/assets/fonts/Effra_LtIt.ttf);
  font-weight: 300;
  font-style: italic;
}
@font-face {
  font-family: Effra;
  src: url(/assets/fonts/Effra_Rg.ttf);
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: Effra;
  src: url(/assets/fonts/Effra_It.ttf);
  font-weight: 400;
  font-style: italic;
}
@font-face {
  font-family: Effra;
  src: url(/assets/fonts/Effra_Md.ttf);
