* {
  box-sizing: border-box;
}
body {
  overflow-x: hidden;
  font-family: 'Effra', sans-serif;
  font-size: 16px;
  color: #3c3c3c;
  background-color: #e7e8e8;
}
body.blue {
  background-color: #004b87;
}
.view-frame {
  position: relative;
  top: 60px;
}
.max-width,
.top-level-arrow-nav,
.section-person-list .person-filter-inputs,
.title-btn-cont {
  width: 100%;
  max-width: 1600px;
  margin: 0 auto;
}
h1 {
  font-size: 6.25rem;
  font-weight: 300;
  margin: 0;
  color: #ffffff;
}
h2 {
  font-size: 4.375rem;
  font-weight: 300;
  margin: 0;
  color: #ffffff;
}
h2.bold {
  font-weight: 400;
}
h3 {
  font-size: 2.5rem;
  font-weight: 400;
  margin: 0;
  color: #ffffff;
}
h4 {
  font-size: 1.75rem;
  font-weight: 400;
  margin: 0;
  color: #4aa7d8;
}
h5 {
  font-size: 1.5rem;
  font-weight: 400;
  margin: 0;
  color: #71c5e8;
}
p {
  font-size: 1.125rem;
  font-weight: 300;
  margin: 0;
}
ul {
  margin: 5px 0 10px;
  padding-left: 1.1em;
}
li {
  margin-bottom: 5px;
  font-weight: 300;
}
a {
  color: #3c3c3c;
  text-decoration: none;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}
sup {
  top: -0.5em;
  font-size: 0.6em;
  padding-left: 0.2em;
}
.blue-link {
  display: inline-block;
  padding: 0 10px;
  font-size: 1.125rem;
  color: #0085ca;
  cursor: pointer;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.blue-link:hover {
  color: #00a9e0;
}
.green {
  color: #77bc1f;
}
button {
  display: inline-block;
  border: none;
  border-radius: 0;
  padding: 12px 20px;
  text-align: center;
  font-size: 1.125rem;
  color: #ffffff;
  background-color: #4daada;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}
button:hover {
  background-color: #004b87;
}
button.dark:hover {
  background-color: #002554;
}
button:active {
  color: #91c6e6;
}
button:active,
button:focus {
  outline: none;
}
button[disabled] {
  opacity: 0.5;
}
button[disabled]:hover {
  background-color: #4daada;
}
button.cancel-btn {
  background-color: transparent;
  color: #e7e8e8;
}
button.cancel-btn:hover {
  background-color: #b9d9eb;
}
button.cancel-btn.dark:hover {
  color: #ffffff;
  background-color: transparent;
}
button.icon-btn {
  line-height: 0;
  padding: 12px;
}
figure {
  margin: 0;
}
input[type="text"],
input[type="number"],
input[type="email"],
input[type="phone"],
input[type="password"],
select,
textarea {
  padding: 10px 15px 8px;
  font-size: 1.5rem;
  font-weight: 300;
  color: #002554;
  border: 1px solid transparent;
  border-radius: 0;
  background-color: #ffffff;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
input[type="text"]:focus,
input[type="number"]:focus,
input[type="email"]:focus,
input[type="phone"]:focus,
input[type="password"]:focus,
select:focus,
textarea:focus,
input[type="text"]:active,
input[type="number"]:active,
input[type="email"]:active,
input[type="phone"]:active,
input[type="password"]:active,
select:active,
textarea:active {
  border-color: #00a9e0;
  outline: none;
}
.input-group label {
  display: block;
  margin: 0 0 15px;
  padding-left: 15px;
  color: #888b8d;
  text-transform: uppercase;
}
.input-group input {
  width: 100%;
}
.search-input {
  position: relative;
}
.search-input .search-icon {
  position: absolute;
  display: inline-block;
  width: 30px;
  height: 49px;
  left: 6px;
  bottom: 1px;
  background-image: url(/assets/img/icon_search.png);
  background-position: 5px 16px;
  background-repeat: no-repeat;
}
.search-input input {
  width: 100%;
  padding-left: 40px;
}
.styled-select-cont {
  position: relative;
  display: inline-block;
  width: 100%;
}
.styled-select-cont select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  display: block;
  width: 100%;
  padding-right: 40px;
}
.styled-select-cont select::-ms-expand {
  display: none;
}
.styled-select-cont select.hidden-back {
  background-color: transparent;
  font-weight: 400;
}
.styled-select-cont:after {
  font-family: 'FontAwesome';
  content: '\f107';
  position: absolute;
  right: 15px;
  bottom: 5px;
  font-size: 2rem;
  color: #0085ca;
}
.edit,
.section-profile .profile-details .details-section.edit:nth-of-type(odd),
.section-profile .profile-main .profile-module.edit,
.section-profile .profile-main .vitals-module .module-data.edit {
  background-color: #e5f3fa;
}
.edit h4,
.section-profile .profile-details .details-section.edit:nth-of-type(odd) h4,
.section-profile .profile-main .profile-module.edit h4 {
  color: #00a9e0;
}
.edit .details-label,
.section-profile .profile-details .details-section.edit:nth-of-type(odd) .details-label,
.section-profile .profile-main .profile-module.edit .details-label,
.edit label,
.section-profile .profile-details .details-section.edit:nth-of-type(odd) label,
.section-profile .profile-main .profile-module.edit label {
  margin-bottom: 3px;
  color: #5b7f95;
}
.edit input,
.section-profile .profile-details .details-section.edit:nth-of-type(odd) input,
.section-profile .profile-main .profile-module.edit input {
  padding: 8px 10px;
}
.edit .button-row,
.section-profile .profile-details .details-section.edit:nth-of-type(odd) .button-row,
.section-profile .profile-main .profile-module.edit .button-row {
  margin: 0;
  text-align: right;
}
.edit .button-row .cancel-btn,
.section-profile .profile-details .details-section.edit:nth-of-type(odd) .button-row .cancel-btn,
.section-profile .profile-main .profile-module.edit .button-row .cancel-btn {
  color: #0085ca;
}
.icon {
  display: inline-block;
  width: 50px;
  height: 50px;
  color: #ffffff;
  background-color: #0085ca;
  background-repeat: no-repeat;
  background-position: center center;
  cursor: pointer;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.icon:hover {
  background-color: #002554;
}
.icon.video-icon {
  background-image: url(/assets/img/icon_video_video.png);
}
.icon.email-icon {
  background-image: url(/assets/img/icon_patient_email.png);
}
.icon.schedule-icon {
  background-image: url(/assets/img/icon_calendar.png);
}
.icon.down-icon {
  background-image: url(/assets/img/icon_file_download.png);
}
.icon.forward-icon {
  background-image: url(/assets/img/icon_file_forward.png);
}
.list-icon {
  position: relative;
  width: 100%;
  height: 100%;
}
.list-icon:hover .bar {
  background-color: #4aa7d8;
}
.list-icon .bar {
  position: absolute;
  width: 100%;
  height: 22%;
  background-color: #888b8d;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.list-icon .bar:nth-child(1) {
  top: 0;
  left: 0;
}
.list-icon .bar:nth-child(2) {
  top: 39%;
  left: 0;
}
.list-icon .bar:nth-child(3) {
  bottom: 0;
  left: 0;
}
.grid-icon {
  position: relative;
  width: 100%;
  height: 100%;
}
.grid-icon:hover .box {
  background-color: #4aa7d8;
}
.grid-icon .box {
  position: absolute;
  width: 44%;
  height: 44%;
  background-color: #c9cacb;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.grid-icon .box:nth-child(1) {
  left: 0;
  top: 0;
}
.grid-icon .box:nth-child(2) {
  right: 0;
  top: 0;
}
.grid-icon .box:nth-child(3) {
  left: 0;
  bottom: 0;
}
.grid-icon .box:nth-child(4) {
  right: 0;
  bottom: 0;
}
.edit-button,
.close-button {
  position: absolute;
  top: 0;
  right: 0;
  padding: 20px;
  line-height: 0;
  cursor: pointer;
}
.edit-button {
  top: 8px;
  right: 10px;
  width: 50px;
  height: 50px;
}
.edit-button:before,
.edit-button:after {
  position: absolute;
  top: 15px;
  left: 15px;
  display: inline-block;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.edit-button:before {
  content: url(/assets/img/icon_edit_light.png);
  opacity: 1;
}
.edit-button:after {
  content: url(/assets/img/icon_edit_light_hover.png);
  opacity: 0;
}
.edit-button:hover:before {
  opacity: 0;
}
.edit-button:hover:after {
  opacity: 1;
}
.edit-button.white:before {
  content: url(/assets/img/icon_edit_dark.png);
}
.edit-button.white:after {
  content: url(/assets/img/icon_edit_dark_hover.png);
}
.close-button {
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.close-button:before {
  content: url(/assets/img/icon_x.png);
  display: inline-block;
}
.close-button:hover {
  background-color: #e5f3fa;
}
.close-button.white {
  padding: 13px;
  height: 46px;
  width: 46px;
  background-color: #ffffff;
}
.close-button.white:before,
.close-button.white:after {
  position: absolute;
  top: 12px;
  left: 12px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.close-button.white:before {
  content: url(/assets/img/icon_x_blue.png);
  opacity: 1;
}
.close-button.white:after {
  content: url(/assets/img/icon_x.png);
  opacity: 0;
}
.close-button.white:hover {
  background-color: #0085ca;
}
.close-button.white:hover:before {
  opacity: 0;
}
.close-button.white:hover:after {
  opacity: 1;
}
.module-edit-button-cont {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  box-shadow: 0 0px 60px 50px rgba(0, 0, 0, 0.6);
}
.module-edit-button-cont .edit-button {
  top: 0;
  right: 0;
}
.done-button {
  position: absolute;
  top: 20px;
  right: 0;
  padding: 10px 25px;
  font-size: 1.125rem;
  color: #0085ca;
  cursor: pointer;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.done-button:hover {
  color: #00a9e0;
}
.call-link:before {
  content: url(/assets/img/icon_patient_phone.png);
  position: relative;
  top: 2px;
  display: inline-block;
  height: 20px;
  margin-right: 8px;
}
.call-link.blue:before {
  content: url(/assets/img/icon_patient_phone_blue.png);
}
.email-link:before {
  content: url(/assets/img/icon_patient_email.png);
  display: inline-block;
  width: 20px;
  margin-right: 8px;
}
.email-link.blue:before {
  content: url(/assets/img/icon_patient_email_blue.png);
  position: relative;
  width: auto;
  top: 4px;
}
.video-link:before {
  content: url(/assets/img/icon_video_video.png);
  position: relative;
  top: 2px;
  display: inline-block;
  margin-right: 8px;
}
.video-link.blue:before {
  content: url(/assets/img/icon_video_blue.png);
  top: 3px;
}
.doc-file-icon,
.video-file-icon {
  display: inline-block;
  width: 100px;
  height: 100px;
  background-color: rgba(193, 215, 227, 0.6);
  background-repeat: no-repeat;
  background-position: center center;
}
.doc-file-icon {
  background-image: url(/assets/img/icon_files_doc.png);
}
.video-file-icon {
  background-image: url(/assets/img/icon_files_play.png);
}
.facebook {
  position: relative;
}
.facebook:before {
  font-family: 'FontAwesome';
  content: '\f09a';
  display: inline-block;
}
.google-plus {
  position: relative;
}
.google-plus:before {
  font-family: 'FontAwesome';
  content: '\f0d5';
  display: inline-block;
}
.section-main-header {
  width: 100%;
  padding: calc(40vh - 286px) 50px calc(60vh - 402px);
  min-height: 280px;
  background-color: #0d305d;
}
.section-main-header .main-title-holder {
  display: inline-block;
  min-height: 244px;
  width: 50%;
  margin-top: 20px;
  padding-top: 65px;
}
.section-main-header .header-meter-holder {
  display: inline-block;
  width: 49%;
}
.section-main-header .meter-figure {
  display: inline-block;
  width: 49%;
  padding-right: 75px;
}
.section-main-header .meter-figure .meter {
  position: relative;
  display: inline-block;
  width: 38px;
  height: 192px;
  background-color: #2d5a4a;
}
.section-main-header .meter-figure .meter .meter-fill {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 80%;
  background-color: #77bc1f;
}
.section-main-header .meter-figure .meter .meter-grid {
  position: relative;
  height: 100%;
  width: 100%;
}
.section-main-header .meter-figure .meter .meter-grid .horizontal-lines,
.section-main-header .meter-figure .meter .meter-grid .vertical-lines {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}
.section-main-header .meter-figure .meter .meter-grid .horiz-line {
  height: 4px;
  width: 100%;
  background-color: #0d305d;
  margin-top: 10px;
}
.section-main-header .meter-figure .meter .meter-grid .vertical-line {
  display: inline-block;
  height: 100%;
  width: 4px;
  background-color: #0d305d;
  margin-left: 10px;
}
.section-main-header .meter-figure .meter-text {
  display: inline-block;
  vertical-align: bottom;
  width: calc(100% - 50px);
  padding-left: 20px;
  font-weight: 300;
}
.section-main-header .meter-figure .meter-text .meter-num {
  font-size: 5.0rem;
  line-height: 1.1em;
  color: #77bc1f;
}
.section-main-header .meter-figure .meter-text .meter-label {
  font-size: 1.15rem;
  font-weight: 300;
  color: #ffffff;
}
.header-imgs {
  display: inline-block;
  vertical-align: top;
  margin-top: 95px;
  width: 49%;
  text-align: center;
}
.header-imgs img {
  display: inline-block;
}
.header-imgs img:first-child {
  margin-right: 10%;
}
.section-header-boxes {
  display: -webkit-flex;
  display: flex;
  color: #ffffff;
  text-align: center;
}
.section-header-boxes .box {
  display: inline-block;
  -webkit-flex: 1 1 0;
  flex: 1 1 0;
  padding: 55px 10px 35px;
  cursor: pointer;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}
.section-header-boxes .box .box-number {
  font-size: 6rem;
  line-height: 1.1em;
}
.section-header-boxes .box .box-label {
  font-size: 1.5rem;
  font-weight: 300;
  margin-bottom: 10px;
}
.section-header-boxes .box .box-hover-text {
  opacity: 0;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.section-header-boxes .box:nth-of-type(odd) {
  background-color: #0085ca;
}
.section-header-boxes .box:nth-of-type(even) {
  background-color: #00a9e0;
}
.section-header-boxes .box:hover {
  background-color: #e35205;
}
.section-header-boxes .box:hover .box-hover-text {
  opacity: 1;
}
.section-header-boxes .box.two-line {
  padding-bottom: 54px;
}
.section-header-boxes .box.no-hover {
  cursor: default;
}
.section-header-boxes .box.no-hover:hover:nth-of-type(odd) {
  background-color: #0085ca;
}
.section-header-boxes .box.no-hover:hover:nth-of-type(even) {
  background-color: #00a9e0;
}
.section-header-boxes .box.no-hover:hover .box-hover-text {
  opactity: 0;
}
.section-header-boxes .box.img-box {
  padding: 45px 10px 10px;
}
.top-level-arrow-nav {
  width: 100%;
  text-align: center;
}
.top-level-arrow-nav a {
  display: inline-block;
  color: #0085ca;
}
.top-level-arrow-nav a.top-level-left,
.top-level-arrow-nav a.top-level-right {
  font-size: 2rem;
  line-height: 1em;
}
.top-level-arrow-nav a.top-level-left {
  float: left;
  padding: 2px 20px 2px 0;
}
.top-level-arrow-nav a.top-level-center {
  padding: 10px;
}
.top-level-arrow-nav a.top-level-right {
  float: right;
  padding: 2px 0 2px 20px;
}
.top-level-arrow-nav a:hover {
  color: #002554;
}
.global-nav {
  position: fixed;
  z-index: 8;
  top: 0;
  left: 0;
  width: 100%;
  max-height: 60px;
  padding-left: 50px;
  background-color: #002554;
}
.global-nav div {
  display: inline-block;
}
.global-nav .left-container {
  text-align: center;
}
.global-nav .left-container .logo {
  display: inline-block;
  margin-right: 39px;
  padding: 20px 0 15px;
  color: #ffffff;
  font-size: 1.4rem;
}
.global-nav .left-container .nav-links a {
  position: relative;
  display: inline-block;
  padding: 25px 20px 15px 38px;
  color: #b9d9eb;
  font-weight: 300;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.global-nav .left-container .nav-links a.current,
.global-nav .left-container .nav-links a:hover {
  color: #0085ca;
}
.global-nav .left-container .nav-links a.current:before,
.global-nav .left-container .nav-links a:hover:before {
  opacity: 0;
}
.global-nav .left-container .nav-links a.current:after,
.global-nav .left-container .nav-links a:hover:after {
  opacity: 1;
}
.global-nav .left-container .nav-links a:before,
.global-nav .left-container .nav-links a:after {
  position: absolute;
  display: inline-block;
  left: 10px;
  top: 25px;
  -webkit-transition: 0.25s;
  transition: 0.25s;
}
.global-nav .left-container .nav-links a:before {
  opacity: 1;
}
.global-nav .left-container .nav-links a:after {
  opacity: 0;
}
.global-nav .left-container .nav-links .link-patients:before {
  content: url(/assets/img/icon_topnav_patientlist.png);
}
.global-nav .left-container .nav-links .link-patients:after {
  content: url(/assets/img/icon_topnav_patientlist_blue.png);
}
.global-nav .left-container .nav-links .link-care:before {
  content: url(/assets/img/icon_topnav_carenetwork.png);
}
.global-nav .left-container .nav-links .link-care:after {
  content: url(/assets/img/icon_topnav_carenetwork_blue.png);
}
.global-nav .left-container .nav-links .link-contacts:before {
  content: url(/assets/img/icon_topnav_contacts.png);
}
.global-nav .left-container .nav-links .link-contacts:after {
  content: url(/assets/img/icon_topnav_contacts_blue.png);
}
.global-nav .left-container .nav-links .link-learn:before {
  content: url(/assets/img/icon_topnav_learn.png);
}
.global-nav .left-container .nav-links .link-learn:after {
  content: url(/assets/img/icon_topnav_learn_blue.png);
}
.global-nav .left-container .nav-links .link-profile:before {
  content: url(/assets/img/icon_topnav_profile.png);
}
.global-nav .left-container .nav-links .link-profile:after {
  content: url(/assets/img/icon_topnav_profile_blue.png);
}
.global-nav .right-container {
  position: absolute;
  right: 0;
  top: 0;
  text-align: center;
}
.global-nav .right-container .nav-search {
  position: relative;
  width: 60px;
  vertical-align: top;
